<template>
    <div class="searchWrap" ref="searchWrap">
        <div class="search">
            <div class="content">
                <div class="topSearch">
                    <form action="/">
                        <van-search v-model="value" placeholder="搜索歌曲" show-action @search="onSearch">
                            <div slot="action" @click="clear">搜索</div>
                        </van-search>
                    </form>
                </div>
                <div v-if="type==1" class="hotSeasrch">
                    <h3 class="title">热门搜索</h3>
                    <ul class="hotSearchList">
                          <li v-for="(item,index) in hotSearchList" :key="index" @click="liClick(item.first)" class="listItem">{{item.first}}</li>
                    </ul>
                </div>
                <div v-if="type==3" class="searchSongsList">
                    <h3 class="title">最佳匹配</h3>
                       <a v-for="(item,index) in searchSongs" :key="index" :href="'#/songDetails/'+item.id">
                        <div class="bdBottom">
                            <div class="lf">
                              <div class="title">{{item.name|toLong(16)}}</div>
                              <div class="desc"><i class="u-hmsprt sghost"></i>{{item|formatSearchSong}}</div>
                            </div>
                            <div class="lr">
                                <span class="u-hmsprt sgchply"></span>
                            </div>
                        </div>
                    </a>   
                </div>
                <footer v-if="type==3" class="m-homeft">
                     <div class="ftwrap">
