-43.15c-2.741 0-4.344.015-4.132-2.595.224-2.775 1.377-17.262 1.377-17.262 3.71-.003 31.04-.176 46.13-1.016.884-.05.896-4.114 0-4.062-15.1.884-46.13.563-46.13.563h-8.264c0 0-.927 15.14-1.376 21.1-.453 6.01 3.186 7.785 8.722 7.785h50.5c-.165-2.75-.962-4.511-3.672-4.511" data-reactid="115"></path><path d="m398.69 71.3c-.585.993 0 1.354 0 1.354 5.778-1.489 13.682-9.586 15.608-22.45h-8.264c0-.0001-1.829 11.738-7.344 21.1" data-reactid="116"></path></g></svg></div>
                    </div>
                </footer>
            </div>
            <div v-if="type==2" class="loding">
                <img  src="../../assets/img/loding.gif" width="40px"> 
            </div>
        </div>
    </div>
</template>
<script>
import BScroll from 'better-scroll'
import Vue from 'vue'
import { Search } from 'vant';
Vue.use(Search);
export default {
    data(){
       return{
            value:"",
            hotSearchList:[],
            searchSongs:[],
            type:1
       } 
    },
    methods:{
        onSearch:async function(){
            this.type=2;
            let res=await this.$.get('search',{keywords:this.value});
            this.searchSongs=res.result.songs;
            this.$store.commit('set_playList',this.searchSongs);
            this.type=3;
        },
        clear:function(){
            this.onSearch();
        },
        liClick:function(first){
            this.value=first;
            this.onSearch();
        },
        _initScroll(){
            if(!this.searchWrap){
                this.$nextTick(()=>{this.searchWrap = new BScroll(this.$refs.searchWrap,{
                click: true})
                });
            }
            else{
                this.searchWrap.refresh();
            }
            
        },
        initHotSearchList:async function(){
            let data=await this.$.get('search/hot');
            this.hotSearchList=data.result.hots;
            this._initScroll();
        }
    },
    created(){
        this.initHotSearchList();
    },
    watch:{
        value(value){
            if(value=='')
            {
                this.type=1;
                this.initHotSearchList();
            }
        }
    }    
}
</script>
<style lang="less" scoped>
.u-hmsprt {
    background: url(../../assets/img/icon2x.png) no-repeat;
    background-size: 166px 97px;
}
@media screen and (-webkit-min-device-pixel-ratio: 3){
    .u-hmsprt {
        background-image: url(../../assets/img/icon3x.png) no-repeat;
    }
}


.searchWrap{
    position: absolute;
    top:104px;
    bottom: 0px;
    width: 100%;
    overflow-y:hidden;
}
.search{
    width: 100%;
}
.hotSeasrch{
    padding: 15px 10px 0;
    &>.title{
        font-size: 12px;
        line-height: 12px;
        color: #666;
        font-weight: 400;
        }
    }

.searchSongsList{
     padding: 15px 10px 0;
     &>.title{
        font-size: 12px;
        line-height: 12px;
        color: #666;
        font-weight: 400;
        padding-left: 10px;
        }
}

.hotSearchList{
    margin: 10px 0 7px;
    font-size: 0px;
    &>.listItem{
            display: inline-block;
            height: 32px;
            margin-right: 8px;
            margin-bottom: 8px;
            padding: 0 14px;
            font-size: 14px;
            line-height: 32px;
            color: #333;
            border: 1px solid #d3d4da;
            border-radius: 32px;
    }
}    

.searchSongsList a{
    display: block;
    padding: 6px 10px;
   
}
.searchSongsList .bdBottom{
    display: flex;
    color: #333;
    border-bottom: 1px solid rgba(0,0,0,0.1);
}
.searchSongsList .lf{
    flex: 1;
}
.searchSongsList .lr{
    flex: 0 0 22px;
    display: flex;
    align-items: center;
    padding: 0 10px;
}
.searchSongsList .lf .title{
    height: 25px;
    line-height: 25px;
    font-size: 17px;
}
.searchSongsList .lf .desc{
    height: 18px;
    line-height: 18px;   
    font-size: 12px;
    color: #888;
}

.sghost{
    display: inline-block;
    width: 12px;
    height: 8px;
    margin-right: 4px;
}
.sgchply {
    display: inline-block;
    width: 22px;
    height: 22px;
    background-position: -24px 0;
}
.loding{
    width: 40px;
    height: 40px;
    margin: 40% auto 0;
}
//footer
.m-homeft {
    position: relative;
    padding-top: 53.3%;
    margin-top: 4px;
    background: url(../../assets/img/recommand_bg.png) no-repeat;
    background-size: contain;
}
.m-homeft .ftwrap{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 1;
    text-align: center;
}
.m-homeft .logo{
    padding-top: 16.9%;
}
.m-homeft .logosvg{
    display: block;
    margin: 0 auto;
    width: 230px;
    height: 44px;
}



</style>




